import React, { Component } from 'react'
import Coin from './Coin/Coin';

class CoinFlipper extends Component {
    constructor(props) {
        super(props);
        this.state = {
            heads: true,
            totalCount: 0,
            headCount: 0,
            tailsCount: 0
        }
        this.handleClick = this.handleClick.bind(this);
        this.flip = this.flip.bind(this);
    }

    handleClick(){
        this.flip();
    }

    flip() {
        let totalVal = this.state.totalCount + 1;
        this.setState({ totalCount: totalVal });

        if (Math.random() >= 0.5) {
            let headVal = this.state.headCount + 1;
            this.setState({ headCount: headVal });
            this.setState({ heads: true });
        } else {
            let tailsVal = this.state.tailsCount + 1;
            this.setState({ tailsCount: tailsVal });
            this.setState({ heads: false });
        }
    }

    render() {
        return (
            <div>
                <h1>Coin Flipper</h1>
                <Coin isHeads={this.state.heads}/>
                <p>Out of {this.state.totalCount} flips, there have been {this.state.headCount} heads and {this.state.tailsCount} tails</p>
                <button onClick={this.handleClick}>Flip</button>
            </div>
        );
    }
}

export default CoinFlipper;